.my-editor .ql-container {
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  background: #fefcfc; }

/* Snow Theme */
.my-editor .ql-snow.ql-toolbar {
  display: block;
  background: #eaecec;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em; }

/* Bubble Theme */
.my-editor .ql-bubble .ql-editor {
  border: 1px solid #ccc;
  border-radius: 0.5em; }

.my-editor .ql-editor {
  min-height: 18em; }

.themeSwitcher {
  margin-top: 0.5em;
  font-size: small; }
  .themeSwitcher .select {
    min-height: 1.6rem; }



